<script>import { getContext } from "svelte";
import InternalEdge from "../../components/Edge/Edge.svelte";
import EdgeContext from "../../components/Edge/EdgeContext.svelte";
const graph = getContext("graph");
const edges = graph.edges;
</script>

{#each Array.from($edges) as [edgeKey, edge] (edgeKey)}
	{@const CustomEdge = edge.component}
	{#if CustomEdge}
		<EdgeContext {edge}>
			<CustomEdge />
		</EdgeContext>
	{:else}
		<InternalEdge {edge} />
	{/if}
{/each}
